<template>
    <div>

        <NavBar />
        
        <div id="login">
           
            <h1 class="heading">Login</h1>
            <form @submit.prevent="login()" class="form-container">

                <hr>
                
                <div v-show="errorShow == true" class="popup-msg">
                    
                    <h2>Login Failed</h2>
                    <p>{{ this.error }}</p>
                </div>

                <div v-show="successShow == true" class="success-msg">
                    <h2> âœ… Login Succesful</h2>
                </div>


                <div class="email input">
                    <label for="email">
                        <p>Email Id ðŸ“§</p>
                    </label>
                    <input :required="true" class="emailInput" type="email" name="email" v-model="email"
                        placeholder="email id">
                </div>
                <div class="password input">
                    <label for="password">
                        <p>Password ðŸ”‘</p>
                    </label>
                    <input :required="true" class="passInput" type="password" name="password" v-model="password"
                        placeholder="password">
                </div>
                <input class="submitInput" id="submit" type="submit" value="login">
                <p class="bottom">
                    Not have an account?
                    <router-link to="/register">Register here</router-link>
                </p>
            </form>


        </div>

    </div>
</template>
<script>

import axios from "axios";
import NavBar from './NavBar.vue';

export default {
    name: "loginPage",
    data() {
        return {
            email: "",
            password: "",
            fullName: "",
            error: "",
            message: "",
            key: 0,
            errorShow: false,
            successShow: false
        };
    },
    methods: {
        closePopup() {
            this.error = "";
        },
        showErrorMessage() {
            this.errorShow = true;
            setTimeout(() => {
                this.errorShow = false;
            }, 2500)
        },
        showSucccessMessage() {
            
            this.successShow = true;
            setTimeout(() => {
                this.successShow = false;
            }, 2500)
        },
        async login() {
            const credentials = {
                email: this.email,
                password: this.password,
            };
            

            
            try {
                
                const result = await axios.post(
                    `${process.env.VUE_APP_BASE_URL}/user/login`,
                    credentials,
                    {
                        headers: {
                            "Content-Type": "application/json",
                        },
                    }
                );
                
                this.error = result.data.message;

                if (result.data.message) {
                    
                    this.showErrorMessage();
                }
                else {
                    await this.savePlayerDetails();
                    this.showSucccessMessage();
                    setTimeout(() => {
                        this.$router.push({ path: "/" });
                    }, 1500)
                }
            }
            catch (err) {
                this.showErrorMessage()
                this.error = err.message;
            }

        },
        async savePlayerDetails() {
            try {
                const result = await axios.get(
                    `${process.env.VUE_APP_BASE_URL}/user/getPlayerDetails/${this.email}`
                );
                localStorage.setItem("userName", result.data.name)
                localStorage.setItem("countryEmoji", result.data.countryEmoji)

            }
            catch (err) {
                console.log(err);
            }
        }
    },
    created:
    {

    },
    components: { NavBar}
};
</script>
